<template>
  <v-container>
    <v-snackbar v-model="snackbar" top :color="err" timeout="2000">{{ alertMessage }}</v-snackbar>
    <v-row>
      <v-col cols="12" md="6" offset-md="3">
        <h1>Latest Posts</h1>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" md="6" offset-md="3">
        <v-textarea
          outlined
          id="create-post"
          name="create-post"
          v-model="text"
          label="Say something..."
          value="Say something..."
        ></v-textarea>
        <v-btn :loading="loading" :disabled="loading" color="primary" @click="createPost">Post</v-btn>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" md="6" offset-md="3">
        <v-divider></v-divider>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" md="6" offset-md="3">
        <v-alert v-if="error" type="error">{{ error }}</v-alert>
      </v-col>
    </v-row>
    <v-row v-if="!posts">
      <v-col cols="12" md="6" offset-md="3">
        <v-skeleton-loader class="mx-auto" type="card, card-heading"></v-skeleton-loader>
      </v-col>
    </v-row>
    <v-row
      v-else
      v-for="(post,index) in posts"
      v-bind:item="post"
      v-bind:index="index"
      v-bind:key="post._id"
      v-on:dblclick="deletePost(post._id)"
    >
      <v-col cols="12" md="6" offset-md="3">
        <v-card color="#1F7087" dark>
          <v-card-title
            class="headline"
          >{{ `${post.createdAt.getDate()}/${post.createdAt.getMonth()}/${post.createdAt.getFullYear()}` }}</v-card-title>
          <v-card-subtitle>{{ post.text }}</v-card-subtitle>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import PostService from "../PostService";
export default {
  name: "Posts",
  data() {
    return {
      posts: [],
      error: "",
      err: "",
      text: "",
      alertMessage: "",
      snackbar: false,
      loading: false
    };
  },
  async created() {
    try {
      this.posts = await PostService.getPosts();
      this.showAlert("Posts Loaded");
    } catch (err) {
      this.error = err.message;
      this.showAlert("Failed to Load Posts", "error");
    }
  },
  methods: {
    showAlert(msg, error = "success") {
      this.alertMessage = msg;
      this.snackbar = true;
      this.err = error;
    },
    async createPost() {
      if (this.text === "") {
        this.showAlert("Post cannot be empty", "error");
        return;
      }
      this.loading = true;
      await PostService.insertPost(this.text);
      this.text = "";
      this.showAlert("Post Created");
      this.loading = false;
      this.posts = await PostService.getPosts();
    },
    async deletePost(id) {
      await PostService.deletePost(id);
      this.showAlert("Post Deleted");
      this.posts = await PostService.getPosts();
    }
  }
};
</script>